___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
    font-size:100%;
}
body{
    --background-gradient:linear-gradient(180deg,#1f1f1f,#284c5a);
    overflow-x: hidden;
    overflow-y: hidden;
    background: #2B292E;
    color:green;
    color: #fafafa;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 1.6rem;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding:6em;
}
body > article {
    max-width:380px;
    width:100%;
   
}
header {
    box-sizing: border-box;
    margin:0 0 10rem 0;
    padding:0.4rem 0 0.3rem 0;
    text-transform: lowercase;
    font-variant:small-caps;
    width:100%;
    text-align: center;
}
#guessed header{
    margin:0 0 1rem 0;
}
.puzzle span {
    border-bottom: 1px solid #534f59;
    display: inline-block;
    font-size: 2rem;
    height: 2.4rem;
    line-height: 2.4rem;
    margin: 0 .1rem;
    text-align: center;
    text-transform: uppercase;
    width: 2.4rem;
}
h1 {
    width:100%;
    text-align: center;
    font-size:4rem;
    font-weight: 800;
    font-family:'montserrat';
} 
p {
    font-weight: 300;
    margin:1rem 0;
}

.puzzle {
    display: flex;
    /* margin-bottom: 4.8rem; */
    flex-wrap: wrap;
    flex:1 1;
    justify-content: center;
    white-space: break-spaces;
    max-width:380px;
    background:#1f1f1f;
    padding:30px 20px;
}
.puzzle > div{
    margin-right:2rem;
    margin-bottom:2rem;    
}
.puzzle > div:last-child{
    margin-right:0;
}


#guessed{
    position: relative;
    /* background:#27252c; */
    padding:0.4rem;
    /* margin:2em 0; */
    min-height:8rem;
    box-sizing: border-box;
    color:#fff;
    background:var(--background-gradient);
}
.alphabet a.cross-out{
    /* text-decoration:line-through !important; */
    color:rgba(255, 255, 255, 0.2);
    background:linear-gradient(-45deg, transparent 50%,#a00 50%, transparent 51% );
}

#guessed .alphabet {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-auto-flow: row;
    grid-auto-columns: min-content;
    position: relative;
}

.guesses-container {
    box-sizing:border-box;
    background:#1f1f1f;
    
    padding:10px 30px;
}
.button-container{
    width:100%;
    padding:0rem;
    display:flex;
    flex-direction: row-reverse;
}
.button {
    background: #7044a0;
   border: none;
   border-bottom: 2px solid #603a88;
   border-radius:6px;
   cursor: pointer;
   color: white;
   font-size: 2rem;
   padding: 8px 24px;
} 

.button:hover {
   background: #5F3A87;
}
footer {
   
    display:grid;
    grid-template-columns:1fr;
    gap:20px;
    font-size:2rem;
}

footer span {
    font-size:2rem;
}
.alphabet a{
    border: 1px solid #534f59;
    border-right:0px;
    border-bottom:0px;
    display: inline-block;
    /* border-radius:6px; */
    font-size: 2rem;
    height: 2.4rem;
    line-height: 2.4rem;
    /* margin: 0 .1rem; */
    text-align: center;
    text-transform: uppercase;
    width: calc(100%);
    aspect-ratio: 1/1;
    height:auto;
    padding:1.5rem;
    text-decoration:none;
    color:#fff;
    /* background:radial-gradient(#111 40%, transparent); */
    background-size:200%;
    background-position: top left;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.alphabet a:nth-of-type(26),
.alphabet a:nth-of-type(25),
.alphabet a:nth-of-type(24),
.alphabet a:nth-of-type(23),
.alphabet a:nth-of-type(22),
.alphabet a:nth-of-type(21){
    border-bottom:1px solid #534f59;
}
.alphabet a:nth-of-type(6n),
.alphabet a:nth-of-type(26){
    border-right:1px solid #534f59;
}
.alphabet a:hover{
    outline:none;
    background:#534f59;
}
.alphabet a:focus {
    background:linear-gradient(-45deg, transparent 50%,#a0a 50%, transparent 51% );
    background-size:10% 0;
}
@media screen and (min-device-width:979px){
    html {
        font-size: 62.5%;
    }
}
@media screen and (min-device-width:320px) and (max-device-width:768px) and (orientation:portrait) {
    body{
        display: flex;
        flex-direction: column;
        justify-content: normal;
        min-height: 100vh;
        width: 100vw;
    }
    h1 {
        font-size:3.4rem;
        width:100%;
        text-align: center;
    }
    .puzzle {
        display: flex;
        flex-direction: row;
        margin-bottom: 4.8rem;
        flex-wrap: wrap;
        width: 100%;

        
    }
    .puzzle span{
        font-size:5rem;
        margin:1rem 0.3rem;
    }
    .puzzle > div{
        margin:0 2rem 2rem 0;    
    }
    .puzzle > div:last-child{
        margin-right:0rem;    
    }
    .alphabet{
        border-bottom: 1px solid #534f59;
        display: inline-block;
        font-size: 3rem;
        height: 2.4rem;
        line-height: 3.4rem;
        box-sizing: content-box;
        text-align: center;
        text-transform: uppercase;
        width: 2.4rem;
        width: auto;
        height: auto;
    }
    .alphabet a{
        border: 1px solid #534f59;
        display: inline-block;
        font-size: 3rem;
        height: 2.4rem;
        line-height: 2.4rem;
        margin: 0 .1rem;
        text-align: center;
        text-transform: uppercase;
        width: 2.4rem;
        width:auto;
        height:auto;
    }
    

}
